@import url('https://fonts.googleapis.com/css2?family=Bangers&display=swap');

* {
  margin: 5px;
  padding: 15px;
}

.centered {
  text-align: center;
}

h1 {
  font-family: 'Bangers', cursive;
  font-size: 50px;
  color: white;
  text-shadow: 1px 1px 10px black;
}

p {
  color: white;
}

section {
  margin: 50px;
  background: linear-gradient(to bottom, gray, rgb(68, 68, 68));
  border-radius: 8px;
  border: 1px solid red;
  box-shadow: 1px 1px 10px black;
}

.section h1, .section img {
  text-align: center;
}

.spiderman img {
  width: 356px;
}

.spiderman button {
  border-radius: 30px;
}
